body{
  background: #F1FAEE;
  color:#1D3557;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
}


.app-container{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.container{
  
  margin: 0 auto;
  padding: 2em;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}
.hide-container{
  display: none;
}
.welcome-container{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5em;
}

.material-symbols-outlined {
  font-variation-settings:
  'FILL' 1,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48;
  font-size: 3rem;

}

.navbar{
  grid-area: nav;
  display: flex;
  justify-content: center;
  padding: 2em;
 /* background-image: url(images/books.jpg);*/
  height: 100%;
  width: 100%;
  background-color: #A8DADC;
  border-bottom: solid 5px #E63946;
}
.search-container{
  width: 80%;
  margin-top: 0em;
}
.text-input{
  border: 1px solid #1D3557;
  color: #1D3557;
  background: #F1FAEE;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-right: none;
  padding-left: 2em;
  grid-area: txt;
  width: 40%;
  height: 49px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 1rem;
}
textarea:focus, input:focus{
  outline: none;
}

#search-btn{
  grid-area: btn3;
  border: 1px solid #1D3557;
  background-color: #F1FAEE;
  color: #1D3557;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  width: 20%;
  height: 53px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
}
#search-btn:active{
  background-color: #1D3557;
  color:#F1FAEE;
}
.plus-btn{
  grid-area: plus;
}
.minus-btn{
  grid-area: minus;
  margin-left: 200px;
  margin-top: 200px;
  padding: 1em;
  width: 100px;
  height: 30px;
  background: none;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color:white;
  border:none;
  cursor: pointer;
}
.plus-btn{
  grid-area: plus;
  padding: 1em;
  width: 100px;
  height: 30px;
  margin-top: 200px;
  background: none;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color:white;
  border:none;
  cursor: pointer;
}
.plus-btn:hover{
  color:aqua;
}
.plus-btn:active{
  color:bisque
}

.minus-btn:hover{
  color:aqua;
}
.minus-btn:active{
  color:bisque
}
/*===============
  BOOK CARD
  ==============*/

.book-card, .bookshelf-card{
  width: 650px;
  margin: 0 auto;
  margin-top: 1em;
  padding: 2em;
  display:flex;
  border: 2px solid #1D3557;
  border-radius: 30px;
  grid-area: book;
}

.book-image{
  width: 50%;
  max-width: 150px;
  height: 250px;
  padding-top: 1em;
}

.book-info-text{
  text-align: left;
  margin-left: 1em;
}

.sub-text{
  display: flex;
  justify-content: space-between;
}
h2{
  font-weight: 700;
  text-align: center;
  font-size: 2rem;
}
h3{
  font-weight: 400;
  font-size: 1rem;
}
p{
  font-weight: 700;
  font-size: .75rem;
  color: #1D3557;
}

.save-book-button{ 
  color:#E63946;
  border-radius: 15px;
  font-weight: 700;
}
.save-book-button:hover{
  cursor: pointer;
}
.save-book-button:active{
  color: #342E37;
}
.bookshelf-title{
  font-size: 2.8em;
}
.bookshelf{
  background: #1D3557;
  color: #F1FAEE;
  padding: 2em;
  border: none;
  font-weight: 700;
  font-size: 1em;
  width: 15em;
}
.bookshelf:hover{
  cursor: pointer;
}
.bookshelf:active{
  background-color: #A8DADC;
  color: #1D3557;
}

@media screen and (max-width : 750px) {
  .container{
    overflow: hidden;
  }
  .book-card, .bookshelf-card{
    max-width:300px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: auto;
  }
  .book-card h2{
    font-size: 1.25rem;
  }

  .sub-text{
    flex-direction: column;
    align-items: center;
  }
  p{
    font-size: .85rem;
  }
  .text-input{
    font-size: .75rem;
    min-width: 80px;
  }
  
  #search-btn{
    font-size: .75rem;
    min-width: 60px;
   
  }
  .book-image{
    height: 20%;
  }
}
  